import React, { PureComponent } from 'react';
import {connect} from 'react-redux';
import DraggableCore from 'react-draggable';
import styled from 'styled-components';
import className from 'classnames';
import * as actions from '../../actions';
import './subWindow.scss';
// import {aboutMe,career,study} from './submenulist/aboutMe';
import {AboutMe} from './contents';

class subWindow extends PureComponent{
    
    constructor(props){
        super(props);
        this.state = {
            contentCode:'',
            modalSizeClass: 'pop_maxsize',
            modalSizeBoolean: false,
        }
    }

    //창 버튼
    selected = (index) => {
        if(this.closeYN !== 'Y') this.props.folderSelected(index);
        this.closeYN = '';
    }
    //창 닫기
    close = (index) => {
        this.closeYN = 'Y';
        this.props.folderClose(index);
    }
    //창 크기 변경
    modalSizeChange = () => {
        if(this.state.modalSizeClass === 'pop_maxsize_on')
            this.setState({...this.state, modalSizeClass:'pop_maxsize'   ,modalSizeBoolean:false,});
        else
            this.setState({...this.state, modalSizeClass:'pop_maxsize_on',modalSizeBoolean:true ,});
    }
    //창 최소화
    modalMinimization = (index) => {
        this.props.folderMini(index);
    }
    //창 오픈 위치 지정
    // modalOpenPosition = (num) => {
    //     return num + 5 * this.props.chosenFolder.z_index;
    // }

    /*스타일 */
    SubTitle = styled.li`
        padding:0.5rem 0 0.2rem;
        span{
            font-weight: bold;
        }
    `;

    domContent = (name) => {
        switch (name) {
            case 'user-home':
                return <AboutMe/>;
        
            default:
                return ( <div className="pop_content">
                            <div className="">
                            <ul>
                                <li></li>
                                <li></li>
                            </ul>
                            </div>
                            <div className="subready">
                                <span> 현재 준비 중입니다. </span>
                            </div>
                        </div>
                        );
        }
    } 
    // domContent = (name) => {
    //     switch (name) {
    //         case 'user-home':
    //             return ( 
    //                     <div className="pop_content">
    //                         <div className="pop_preview">
    //                             <div className="menu-list">
    //                                 <ul>
    //                                     <li className="user-menu-1" onClick={() => this.setState({ ...this.state, contentCode: aboutMe.code}) } >
    //                                         {aboutMe.name}
    //                                     </li>
    //                                     <li className="user-menu-2" onClick={() => this.setState({ ...this.state, contentCode: career.code}) }>
    //                                         {career.name}   
    //                                     </li>
    //                                     <li className="user-menu-3" onClick={() => this.setState({ ...this.state, contentCode: study.code}) }>
    //                                         {study.name}
    //                                     </li>
    //                                 </ul>
    //                             </div>
    //                             <div className="sub-content">
    //                                 {this.aboutMeContent(this.state.contentCode)}
    //                             </div>
    //                         </div>
    //                     </div>
    //                     );
        
    //         default:
    //             return ( <div className="pop_content">
    //                         <div className="">
    //                         <ul>
    //                             <li></li>
    //                             <li></li>
    //                         </ul>
    //                         </div>
    //                         <div className="subready">
    //                             <span> 현재 준비 중입니다. </span>
    //                         </div>
    //                     </div>
    //                     );
    //     }
    // } 



    createModal = () => {
        return (
              <DraggableCore  
                handle=".pop_head" 
                disabled={this.state.modalSizeBoolean}
                >
                <div className={className("modal", { "max_modal": this.state.modalSizeBoolean })}
                     onMouseDownCapture={ () => {this.selected(this.props.chosenFolder.index) } }
                    style={{ zIndex: this.props.chosenFolder.z_index}}
                    >
                    {/* left: `${this.modalOpenPosition(5)}%`, top: `${this.modalOpenPosition(10)}%` */}
                    <div className="pop_head">    
                        <div className="pop_folder_img"> 
                            <img src={this.props.chosenFolder.imgUrl} alt="" />
                        </div>
                        <div className="pop_title">
                            <span className={className({"text_color_ddd" : !this.props.chosenFolder.chosen})}>
                                { this.props.chosenFolder.text }
                            </span>
                        </div>
                        <div className="pop_mng">
                            <ul>
                                <li onClick={()=> {this.modalMinimization(this.props.chosenFolder.index) }}>
                                    <input type="button" className="pop_minsize"/>
                                </li>
                                <li onClick={() => {this.modalSizeChange()} }>
                                    <input type="button" className={this.state.modalSizeClass}/>
                                </li>
                                <li onClick={ () => { this.close(this.props.chosenFolder.index) } }>
                                    <input type="button" className="pop_close" />
                                </li>
                            </ul>
                        </div>
                    </div>
                    {/*
                    <nav>
                        <div>
                            <input className="pop_navi" type="text" alt="" />
                        </div>
                        <div>
                            <input className="pop_search" type="text" alt="" />
                        </div>
                    </nav> 
                    */}
                    { this.domContent(this.props.chosenFolder.name) }                    
                </div>
            </DraggableCore>
            )
    }
    
    render(){
        return(
            <div>
                {this.createModal()}
            </div>
        )   
    }
}

const mapToDispatch = (dispatch) =>({
    folderSelected  : (index) => dispatch(actions.folderSelected(index)),
    folderClose     : (index) => dispatch(actions.folderClose(index)),
});

export default connect(null,mapToDispatch)(subWindow);