import React, { Component } from 'react'
import classNames from 'classnames';
import loginViewScss from './loginView.scss';
import {connect} from 'react-redux';
import * as actions from '../../actions';

const scss = classNames.bind(loginViewScss);
class LoginView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      password : ''
    }
  }
  onSubmit = () =>{
    const password = document.getElementById('login-password').value;
    if(password){
      this.props.loginChecked(password);
    }
  }
  handlePasswordKeyUp = (value) =>{
    if (value.which === 13) this.onSubmit();
  }

  render() {
    return (
      // ? 'hidden' : ''}
      <div id={scss(`login-wrap`)} className={scss(!this.props.redux_login.login.user.name || 'hidden')}>
        <div className={scss('login-bg')}/>
        <div className={scss('login-box')}>
          <p className={scss('login-profile-photo')} />
          <p className={scss('login-profile-title')}>
            USER-NAME
          </p>
          <div className={scss('loginner')}>
            {/* <form onSubmit={this.onSubmit}> */}
            <input id="login-password" onKeyUp={this.handlePasswordKeyUp} type="password" placeholder="암호"/>
              {/* <input type="submit" value=">" /> */}
            <input type="button" value=">" onClick={this.onSubmit} />
            {/* </form> */}
          </div>
          <p className={scss('login-options')} />
        </div>
      </div>
    )
  }
}

function mapStateToProps(state){
  return state;
}
const mapToDispatch = (dispatch) =>({
  loginChecked  : (value) => dispatch(actions.loginChecked(value)),
});

export default connect(mapStateToProps,mapToDispatch)(LoginView);