import React, { PureComponent } from 'react';
import {connect} from 'react-redux';
import contextmenuCss from './contextmenu.scss';
import classNames from 'classnames';
// import SubWindow f rom '../subwindow';

const scss = classNames.bind(contextmenuCss);
class Contextmenu extends PureComponent{
    state = {
        visible: false,
        isPowerShell: false
    };
    
    componentDidMount() {
        document.addEventListener('contextmenu', this._handleContextMenu);
        document.addEventListener('click', this._handleClick);
        document.addEventListener('scroll', this._handleScroll);
    };

    componentWillUnmount() {
      document.removeEventListener('contextmenu', this._handleContextMenu);
      document.removeEventListener('click', this._handleClick);
      document.removeEventListener('scroll', this._handleScroll);
    }
    
    _handleContextMenu = (event) => {
        event.preventDefault();
        
        this.setState({ visible: true });
        
        const clickX = event.clientX;
        const clickY = event.clientY;
        const screenW = window.innerWidth;
        const screenH = window.innerHeight;
        const rootW = this.root.offsetWidth;
        const rootH = this.root.offsetHeight;
        
        const right = (screenW - clickX) > rootW;
        const left = !right;
        const top = (screenH - clickY) > rootH;
        const bottom = !top;
        
        if (right) {
            this.root.style.left = `${clickX + 5}px`;
        }
        
        if (left) {
            this.root.style.left = `${clickX - rootW - 5}px`;
        }
        
        if (top) {
            this.root.style.top = `${clickY + 5}px`;
        }
        
        if (bottom) {
            this.root.style.top = `${clickY - rootH - 5}px`;
        }
    };

    _handleClick = (event) => {
        const { visible } = this.state;
        const wasOutside = !(event.target.contains === this.root);
        if (wasOutside && visible) this.setState({ visible: false, });
    };

    _handleScroll = () => {
        const { visible } = this.state;
        if (visible) this.setState({ visible: false, });
    };
    
    _onRefresh = () => {
        //새로고침
        return document.location.reload(true);
    }
    _onBack = () => {
        //뒤로가기
        return window.history.go(-1);
    }

    _onSuContextMenu = (on) => {
        
    }

    _handleSortList = () => {

    }

    _onPowerShell = () => {
        
    }

    render() {
        const { visible } = this.state;
        return (visible || null) &&
            <div ref={ref => {this.root = ref}} className={scss("contextMenu")}>
                    <div className="contextMenu--option">새폴더</div>
                    <div className="contextMenu--option">보기</div>
                    <div className="contextMenu--option" onMouseOver={this._onSuContextMenu(true)} onMouseOut={this._onSuContextMenu(false)}>정렬기준</div>
                    <div className="contextMenu--option" onClick={this._onRefresh}>새로고침</div>
                    <div className="contextMenu--separator" />
                    <div className="contextMenu--option contextMenu--option__disabled">붙여넣기</div>
                    <div className="contextMenu--option" onClick={this._onPowerShell}>여기에 PowerShell 창 열기</div>
                    <div className="contextMenu--separator" />
                    <div className="contextMenu--option" onClick={this._onBack}>뒤로가기</div>
                </div>
    };
}  

function mapStateToProps(state){
    return state;
}

export default connect(mapStateToProps)(Contextmenu);;