import * as types from './actionType';

export function folderOpen(index){
    return {
        type : types.FOLDER_OPEN,
        index
    }
}

export function folderSelected(index){
    return {
        type : types.FOLDER_SELECTED,
        index
    }
}

export function folderClose(index){
    return {
        type : types.FOLDER_CLOSE,
        index
    }
}

export function folderMini(index){
    return {
        type : types.FOLDER_MINI,
        index
    }
}

export function loginChecked(value){
    return {
        type: types.LOGIN_CHECKED,
        value
    }
}

