import React, { PureComponent } from 'react';
import {connect} from 'react-redux';
import taskbarCss from './taskbar.scss';
import classNames from 'classnames';


const scss = classNames.bind(taskbarCss);
let clockSatus = 'FIRST';

class Taskbar extends PureComponent{
    
    state = {
          amPm:null,
          currentHours:null,
          currentMinute:null,
          calendar:null,
          clockSec:null,
          soundBarHeight: 0,
          soundValue: 10,
    }

    componentWillReceiveProps(prev){
        if(prev.appClickInfo && prev.appClickInfo.indexOf('sound') <= -1){
            const soundBarEl = document.getElementById('sound-bar');
            const soundBarClass = soundBarEl.classList;
            soundBarClass.add('hidden');
        } 
    }
    // 자릿수 맞춰주기
    addZeros = (num, digit) => { 
        let zero = '';
        num = num.toString();
        if ( num.length < digit ) 
            for (let i = 0; i < digit - num.length; i++) 
                zero += '0';
        return zero + num;
    }
  
    //setState가 componentDidMount후에 일어나기 때문에 setInterval 시간초를 맞추기 위한 처리
    componentWillMount(){ //getderivedstatefromprops
        this.handleChangeTime();
    }
    componentDidMount(){//최초 실행 한번 실행
        setTimeout(this.handleChangeTime, this.state.clockSec);
    }
    componentDidUpdate(){//1분마다 실행
        setInterval(this.handleChangeTime, this.state.clockSec);
    }

    handleChangeTime = () => {
        let clockSec =0;
        const currentDate = new Date(); // 현재시간
        let calendar = currentDate.getFullYear() + "-" + this.addZeros((currentDate.getMonth()+1), 2) + "-" + this.addZeros(currentDate.getDate(),2); // 현재 날짜
        let currentHours   = this.addZeros(currentDate.getHours(),2); 
        let currentMinute  = this.addZeros(currentDate.getMinutes(),2);

        let currentSeconds = this.addZeros(currentDate.getSeconds(),2);
        
        let amPm = '오전'; //초기값 AM
        if(currentHours >= 12){ //시간이 12보다 클 때 PM으로 세팅, 12를 빼줌
            amPm = '오후';
            currentHours = this.addZeros(currentHours - 12,2);
        }
        if (amPm === '오후' && parseInt(currentHours) === 0) currentHours = 12;
        if(clockSatus != null){
            clockSec = 60000 - (currentSeconds * 1000); 
            clockSatus = null;
        }else clockSec = 60000;

        // console.log('현재 시간 : '+currentHours+ ' : ' + currentMinute);
        this.setState({
            amPm,
            currentHours,
            currentMinute,
            calendar,
            clockSec:parseInt(clockSec),
        })
    };
    
    onSoundToggle = () =>{
        const soundBarEl = document.getElementById('sound-bar');
        const soundBarClass = soundBarEl.classList;
        if(soundBarClass.value.indexOf('hidden') > -1)
            soundBarClass.remove('hidden');
        else
            soundBarClass.add('hidden');
    }

    quickIconClick = (name,index) => {
        switch (name) {
                case 'tistory':
                    if(!window.confirm('블로그로 이동하시겠습니까?')) return false;
                    return window.open('http://backback.tistory.com/');
                    case 'github':
                    if(!window.confirm('깃 허브로 이동하시겠습니까?')) return false;
                    return window.open('https://github.com/FactAttacker?tab=repositories/');    
                default:
                    this.props.folderOpen(index); 
                    this.props.folderSelected(index);
                    break;
        }
    }
    soundBarPosition = (element) =>{
        if(element){
            this.setState({
                ...this.state,
                soundBarHeight: element.offsetHeight
            })
        }
    }

    onSoundVolume = (el) => {
        this.setState({
            ...this.state,
            soundValue: el.target.value
        })
        // const soundBtnClass = document.getElementsByClassName('sound_on_btn_on')[0].classList;
        let soundBtnClass = document.getElementsByClassName('sound_on_btn_on');
        console.log(soundBtnClass[1]);
        for(let i =0; soundBtnClass.length > i ; i++){
            if(parseInt(el.target.value) === 0){
                soundBtnClass[i].classList.add('sound_on_btn_off');
            }else{
                soundBtnClass[i].classList.remove('sound_on_btn_off');
            }
        }
    }

    render() {
        return (
            <div className={scss("footer")}>
                <div id={scss("sound-bar")} className={scss('sound hidden')} ref={(ref) => this.soundBarPosition(ref)} style={{top : `-${this.state.soundBarHeight}px`}}>
                    <p className={scss("sound-name")}>스피커(FactAttacker smartAudio)</p>
                    <div className="sound">
                        <p className={scss('sound-icon sound_on_btn_on')} />
                        <input className={scss("sound-range")} type="range" min="0" max="100" defaultValue={this.state.soundValue} onChange={this.onSoundVolume}/>
                    </div>
                </div>
                <div className="task_bar">
                    {/* <!-- 윈도우 시작 버튼 --> */}
                    <input className={scss('start_bnt')} type="button" />

                    {/* 퀵 아이콘 */}
                    <ul className="quick_icon">
                        {
                            this.props.redux_folder.folder.map((key,i) => {
                                return (<li key={"quick-icon-"+i} className={classNames({"open_icon":key.open,"active_icon": key.chosen})}
                                            onClick={() => {this.quickIconClick(key.name,i)} }
                                        > 
                                            <img src={key.imgUrl} alt=""/>
                                        </li>);
                            })
                        }
                    </ul>
                    <div className={scss('right_box')}>
                        {/* <!-- 스피커 --> */}
                        <div className={scss('sound_box')}>
                            <input className={scss('sound_on_btn sound_on_btn_on')} onClick={() => this.onSoundToggle()} type="button" alt="사운드" />
                        </div>

                        {/* {날짜 시간} */}
                        <div className={scss('date_box')}> 
                            <p className={scss('clock')}><span>{this.state.amPm + ' ' + Number(this.state.currentHours) + ':' + this.state.currentMinute }</span></p>
                            <p className={scss('date')}><span>{this.state.calendar}</span></p>
                        </div>
                    </div>
                
                </div>
            </div>
        )
    }
}

function mapStateToProps(state){
    return state;
}

export default connect(mapStateToProps)(Taskbar);
