import { combineReducers } from 'redux';
import Reducer_folder from './reducer_folder';
import Reducer_login from './reducer_login';
import * as types from '../actions/actionType';

const root_reducer = {
    folder : Reducer_folder,
    login: Reducer_login,
} 

const array = [];
function redux_folder (state = root_reducer, action) {
    console.log(' > redux_folder');
    const {folder} = state; 
    switch(action.type){
        case types.FOLDER_MINI:
            return {}
        case types.FOLDER_OPEN:
            return { ...state, folder : folder.map( (key,i) => {
                if(i === action.index)
                    return {...key, open:true}
                else
                    return {...key}
                })};
        
        
        case types.FOLDER_CLOSE: 
            return {...state, folder : folder.map( (key,i) => {
                if(i === action.index)
                    return {...key, open:false,chosen:false}
                else
                    return {...key}
                })};

        case types.FOLDER_SELECTED: 
            const idx = array.indexOf(action.index);
            if(idx > -1){
                array.splice(idx, 1);
            }
            return {...state, folder : folder.map( (key,i) => {
                if(i === action.index){
                    array.push(i);
                    return {...key, open:true, z_index: array.indexOf(key.index)+1 ,chosen:true}
                }else
                    return {...key, z_index: array.indexOf(key.index)+1, chosen:false}
                })};
        default: 
            return state;
    }
}
function redux_login (state = root_reducer, action) {
    console.log(' > redux_login');
    const {login} = state; 
    switch(action.type){
        case types.LOGIN_CHECKED:
            try{
                let user;
                login.usersInfo.some( data => {
                    if(data.password === action.value) return user = data }
                );
                if(!user) return {...state};
                delete user.password;
                return { ...state,  login : { user, usersInfo : login.usersInfo } };
            }catch(e){
                console.log(e);
            }
            break;
        default: 
            return state;
    }
}

export default combineReducers({redux_folder,redux_login});