import React, { PureComponent , Fragment} from 'react';
// import Folder from '../conponent/folder';
// import Taskbar from '../conponent/taskbar';
// import Contextmenu from '../conponent/contextmenu';
// import { Folder, Taskbar, Contextmenu} from '../conponent';
import Folder from '../conponent/folder';
import Contextmenu from '../conponent/contextmenu';
import LoginView from '../conponent/loginView';
import Taskbar from '../conponent/taskbar';

import './App.scss'; 

import * as actions from '../actions';
import { connect } from 'react-redux';
import { ThemeConsumer } from 'styled-components';

class App extends PureComponent {
  constructor(props){
    super(props);
    this.state = {
      appClickInfo: null
    };
  }

  render() {
    const {folderOpen,folderSelected} = this.props;
    return (
      <Fragment>
        <LoginView />
        <div 
        className={`App ${ this.props.redux_login.login.user.name != null || 'blur' }`} 
        onClick={el => this.setState({ appClickInfo : el.target.className})}>
          <Contextmenu 
            folderOpen={folderOpen}
            folderSelected ={folderSelected}         
          />
          <Folder
            folderOpen     ={folderOpen}         
            folderSelected ={folderSelected}         
            />
          <Taskbar
            folderOpen     ={folderOpen}         
            folderSelected ={folderSelected} 
            appClickInfo = {this.state.appClickInfo}        
            // folderClose    ={folderClose}         
          />
        </div>
      </Fragment>
    );
  }
}
function mapStateToProps(state){
  return state;
}
const mapToDispatch = (dispatch) =>({
     folderOpen    : (index) => dispatch(actions.folderOpen(index)),
     folderSelected: (index) => dispatch(actions.folderSelected(index)),
     folderClose   : (index) => dispatch(actions.folderClose(index)),
});

export default connect(mapStateToProps, mapToDispatch)(App);